
#app {
  font-family: 微软雅黑, Helvetica, Arial, sans-serif;
  font-size: 12px;
}

body,html,p {
  margin: 0;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.panel {
  width: 100%;
  display: block;
}
.submit-block {
  position: relative;
}
.submit {
  position: absolute;
  width: 100px;
  height: 30px;
  top: 10px;
  left: 50%;
  margin-left: -50px;
}
.input-body {
  position: relative;
}
.input-block {
  position: absolute;
  top: 0;
  width: 100%;
  padding: 48px 0 0 0;
}
.list {
  display: block;
  margin: .05rem .45rem 0;
  display: flex;
  display: -webkit-flex;
  display: -moz-flex;
  display: -o-flex;
  display: -ms-flex;
}
.list-inner {
  flex: 1;
  display: flex;
  display: -webkit-flex;
  display: -moz-flex;
  display: -o-flex;
  display: -ms-flex;
  height: .22rem;
}
.list-name {
  flex: 2;
}
.list-name-long {
  flex: 4;
}
.list-input {
  flex: 5;
  border: black solid 1px;
  position: relative;
}
.list-input input{
  background: transparent;
  border: none;
  width: 91%;
}
.list-input select {
  background: transparent;
  border: none;
  width: 123%;
}
.list-select-modal {
  width: 81%;
  overflow: hidden;
  display: inline-block;
}
.triangle{
  width:0;
  height:0;
  border-width:5px;
  border-style:solid dashed dashed dashed;
  border-color:#e66161 transparent transparent transparent;
  position: absolute;
  right: 2px;
  top: 7px;
}
